import "./style.css";

/**
 *   -    ,      
 * @param {*} param0 
 * @returns 
 */
export const DefectiveListItem = ({ defectiveListItem, index, defectiveListLength }) => {
    return (
        <span class="content-defective-list-span-item">
            {defectiveListItem.values.data.typeThing} {defectiveListItem.values.data.numberThing}  {defectiveListItem.values.data.whereAddress}  {defectiveListItem.values.data.fromAddress}. {defectiveListItem.values.differenceWeight &&  <span>   .</span>}   {defectiveListItem.values.giviWeight},  {defectiveListItem.values.data.actualWeight}. {defectiveListItem.values.description}
            {defectiveListLength - 1 !== index && <span>;</span>}
            {defectiveListLength - 1 === index && <span>.</span>}
        </span>
    )
}