export const getDataAirBags = (dataInvoiceListItem) => {
  const regExp = /\s\s\d{1,3}\s(\d{1,3})\s/gim;
  let arrResult = regExp.exec(dataInvoiceListItem);
  // .  ,    0
  const airBagsAmount = arrResult ? arrResult[1] : 0;
  return airBagsAmount;
};
