import { InvoiseListOutput } from "../InvoiseListOutput";
import "./style.css";

export const Where = ({ invoiseList, flight, timeArrival, fromGA, dateArrival, typeAct }) => {
    const hours = timeArrival.substring(0, 2);
    const minutes = timeArrival.substring(3, 5);
    const { day, mounthNumber, year } = dateArrival;

    return (
        <span class="content-header">
            {invoiseList.length === 1 && <div class="content-header-item"><span class="content-header-indent"></span>  {flight?.number ?? " "}  {flight?.city ?? " "}  {hours}. {minutes}.
                     {invoiseList[0].number}  {invoiseList[0].thingAmount}   {invoiseList[0].totalWeight}
                 {invoiseList[0].date}. {invoiseList[0].time && <span>{invoiseList[0].time} </span>}
                 {invoiseList[0].ourAddress}  {invoiseList[0].citiAddress}.
            </div>
            }
            {invoiseList.length > 1 && <div>
                <p class="content-header-item"><span class="content-header-indent"></span>  {flight?.number ?? " "}  {flight?.city ?? " "}  {hours}. {minutes}.
                        :</p>
                <InvoiseListOutput invoiseList={invoiseList} />
            </div>
            }
            {
                (fromGA && typeAct === "type52") && <div class="content-header-item"><span class="content-header-indent"></span>   {`${day}.${mounthNumber}.${year}.`}  {hours}. {minutes}.         :
                </div>
            }
            {
                (fromGA && typeAct === "tp51-d") && <span class="content-header-item"><span class="content-header-indent"></span>   {`${day}.${mounthNumber}.${year}.`}  {hours}. {minutes}.     
                </span>
            }
        </span>
    )
}