import "./style.css";

export const InvoiseListOutputItem = ({ invoiseListOutputItem, invoiseListOutputLength, index }) => {
    return (
        <p class="content-header-indent-some">
            <span class="content-header-span-indent">- </span>{invoiseListOutputItem.number}  {invoiseListOutputItem.thingAmount}   {invoiseListOutputItem.totalWeight}
             {invoiseListOutputItem.date}. {invoiseListOutputItem.time}  {invoiseListOutputItem.ourAddress}  {invoiseListOutputItem.citiAddress}
            {invoiseListOutputLength - 1 !== index && <span>;</span>}
            {invoiseListOutputLength - 1 === index && <span>.</span>}
        </p>
    )
}